
#about {
  text-align: left;
  position: absolute;
  top: 16%;
  left: 20%;
  z-index: 8;
  color: white;
  font-size: 34px;
  line-height: 130%;
  animation: fadeIn 2s 0.5s ease both;
}
.mar {
  margin: 50px 0 50px;
}
.mar h3 {
    font-size: 36px;
    padding: 42px 0 26px;
    font-weight: 500;
    color: #666;
}
.mar p {
    line-height: 32px;
    font-size: 14px;
    color: #8d8d8d;
    text-align: center;
}



