
header {
  width: 100%;
  height: 100px;
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  z-index: 9;
}
#nav {
  position: absolute;
  width: 100%;
  height: 100%;
}
.logo {
  width: 54px;
  height: 54px;
  position: relative;
  left: 15%;
  top: 25%;
  cursor: pointer;
}
.nav-item ul {
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;
  left: 25%;
  bottom: 20px;
}
li {
  height: 26px;
  line-height: 26px;
  position: relative;
  display: inline-block;
  margin-left: 44px;
  color: white;
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  opacity: 0.8;
}
.selected {
  color: yellow;
}
.nav-in {
  animation: navin .5s 0s ease both;
}
@keyframes navin {
0% {
    color: #a3daa5;
}
100% {
    color: #0bfb13;
}
}



