
#download {
  text-align: left;
  position: absolute;
  top: 38%;
  left: 29%;
  z-index: 8;
  color: white;
  font-size: 28px;
  color: #e9e7e5;
  font-weight: 300;
  margin: 0;
  padding: 0;
  animation: none;
}
.content .banner {
  position: relative;
  width: 100%;
  top: -13%;
  left: 0;
  margin: 0;
  padding: 0;
}
.content .banner h3 + p {
    font-size: 16px;
    color: #e6e5e3;
    font-weight: 300;
    position: absolute;
    top: 44%;
    left: 29%;
    z-index: 8;
    margin: 0;
    padding: 0;
}
.teach {
  margin-bottom: 7%;
}
.teach h3 {
    padding: 50px 0;
    font-weight: 500;
    font-size: 30px;
    text-align: center;
    color: #333333;
}
.mar {
  margin: 50px 0 50px;
}
.mar p {
    line-height: 32px;
    font-size: 14px;
    color: #8d8d8d;
    text-align: center;
}
.step {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.container {
  width: 300px;
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
}
.container img {
    width: 300px;
    height: 300px;
}
.container h3 {
    margin-bottom: 10px;
    text-align: center;
    font-size: 18px;
    color: #333333;
    font-weight: 500;
    padding: 0;
}
.container p {
    line-height: 30px;
    font-size: 14px;
    color: #333333;
    text-align: center;
    opacity: 0.6;
}
em {
  font-style: normal;
  font-size: 22px;
  vertical-align: text-bottom;
  line-height: 27px;
}



