
.content .banner h1 {
  position: absolute;
  width: 100%;
  top: 40%;
  z-index: 8;
  color: white;
  font-size: 66px;
  animation: fadeIn 2s 0.5s ease both;
}
.introduce {
  position: relative;
  width: 100%;
  height: 100%;
  top: -10%;
}
.introduce img {
    width: 400px;
    height: 400px;
}
.pic {
  position: relative;
}
.pic img {
    left: -15%;
    position: relative;
}
.ad {
  position: absolute;
  top: 10%;
  left: 55%;
  width: 336px;
}
.ad h3 {
    text-align: left;
    font-size: 36px;
    color: #585858;
    font-weight: 500;
}
.word {
  font-size: 14px;
  color: #4b4b4b;
  line-height: 22px;
  text-align: left;
  margin: 0;
  padding: 0;
  padding-bottom: 10px;
}
.mar1 {
  margin: 50px 0 50px;
}



