
.jobContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.jobEmail {
  margin: 60px 0 100px;
}
.jobEmail p {
    display: block;
    height: 24px;
    line-height: 24px;
    color: #b8b8b8;
    font-size: 14px;
    margin: 0;
    padding: 0;
    cursor: pointer;
}
.email {
  margin-top: 40px;
}
.email div {
    width: 134px;
    height: 58px;
    line-height: 58px;
    display: block;
    margin: 0 auto;
    text-align: center;
    border: 1px solid #e5e5e5;
    font-size: 14px;
    color: #333333;
    border-radius: 30px;
    cursor: pointer;
}
.email div:hover {
      background-color: #ffc107;
}
.email div + p {
      margin-top: 40px;
      cursor: auto;
}
.jobList {
  width: 100%;
}
.jobList h3 {
    font-size: 18px;
    text-align: center;
    color: #585858;
    margin-bottom: 16px;
}
.jobList p:hover {
    color: #585858;
}
#jobH3 {
  line-height: 86px;
  top: 20%;
  text-align: center;
  font-size: 45px;
  display: table-cell;
  vertical-align: middle;
  animation: fadeIn 2s 0.5s ease both;
  position: absolute;
  font-weight: bold;
  left: 38%;
  z-index: 8;
  color: #ffffff;
}
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}



